import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Card } from 'antd';

const researchProjects = [
  {
    id: "rp1",
    title: "Artificial Intelligence",
    image: "/research_images/r_1.jpg",
    description: "Research project 1 description"
  },
  {
    id: "rp2",
    title: "Science Crawler",
    image: "/research_images/r_2.jpg",
    description: "Research project 2 description"
  },
  {
    id: "rp3",
    title: "Biophysics",
    image: "/research_images/r_3.jpg",
    description: "Research project 3 description"
  },
  // Add more projects here
];

function Research() {
  let navigate = useNavigate();

  const handleProjectClick = (projectId) => {
    navigate(`/research/${projectId}`);
  };

  return (
    <div>
      <h2>Research Projects</h2>
      <div className="research-preview-container">
        {researchProjects.map((project) => (
          <Card
            key={project.id}
            hoverable
            style={{ width: 240 }}
            cover={<img alt={project.title} src={project.image} />}
            onClick={() => handleProjectClick(project.id)}
          >
            <Card.Meta title={project.title} description={project.description} />
          </Card>
        ))}
      </div>
    </div>
  );
}

export default Research;
